import React, { useState } from "react"
import styled from "styled-components"
import { Emoji } from "./game/Util"

// Inspired by https://github.com/distillpub/post--communicating-with-interactive-articles/blob/master/src/diagrams/horse.svelte

const Container = styled.div`
  border: 1px solid gray;
  border-radius: 5px;
  background-color: whitesmoke;
`
const PictureAndInformation = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`
const Picture = styled.img`
  margin: 25px;
  height: 250px;
  width: auto;
  border-radius: 5px;
`
const Information = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 15px 15px 0;
  /* padding: 15px; */
`
const Controls = styled.div`
  display: flex;
  flex-direction: row;
`
const Slides = styled.div`
  text-align: center;
  padding: 10px;
  border-top: 1px solid gray;
`
const Slide = styled.img`
  height: 100px;
  width: auto;
  margin-right: 5px;
  cursor: crosshair;
  padding-bottom: 3px;
  border-bottom: ${({ selected }) =>
    selected ? "5px solid midnightblue" : "5px solid rgba(0,0,0,0)"};
`

const Rotated = ({ rotation = 90, children, style }) => (
  <div style={{ ...style, transform: `rotate(${rotation}deg)` }}>
    {children}
  </div>
)

const RotatedText = (props) => (
  <Rotated style={{ fontSize: props.size }} {...props}></Rotated>
)

const StyledAnimateButton = styled.button`
  border: 2px solid slategray;
  border-radius: 50%;
  outline: none;
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
`

const ButtonSymbols = {
  play: <Emoji symbol="0x25B6" style={{ paddingLeft: 3 }} />,
  pause: <RotatedText size={23}>=</RotatedText>,
}

const AnimateButton = ({ status = "stopped", onClick }) => {
  const stateSymbols = {
    stopped: ButtonSymbols.play,
    playing: ButtonSymbols.pause,
  }
  return (
    <StyledAnimateButton onClick={onClick}>
      {stateSymbols[status]}
    </StyledAnimateButton>
  )
}

const ImageAnimation = ({ images, info, width, delay = 150 }) => {
  const [selectedIndex, setSelectedIndex] = useState(0)
  const [animating, setAnimating] = useState(false)
  React.useEffect(() => {
    if (animating) {
      const nextIndex =
        selectedIndex < images.length - 1 ? selectedIndex + 1 : 0
      setTimeout(() => setSelectedIndex(nextIndex), delay)
    }
  }, [animating, selectedIndex, setSelectedIndex, images, delay])

  return (
    <Container style={{ width }}>
      <PictureAndInformation>
        <Picture src={images[selectedIndex]} alt="" />
        <Information>
          <div>{info}</div>
          <Controls>
            <AnimateButton
              selectedIndex={selectedIndex}
              status={animating ? "playing" : "stopped"}
              onClick={() => setAnimating(!animating)}
            />
            <input
              type="range"
              min={0}
              max={images.length - 1}
              step={1}
              value={selectedIndex}
              style={{ outline: "none" }}
              onInput={(e) => setSelectedIndex(parseInt(e.target.value))}
            />
          </Controls>
        </Information>
      </PictureAndInformation>
      <Slides>
        {images.map((x, i) => (
          <Slide
            key={`slide-${i}`}
            src={x}
            onMouseOver={() => {
              if (!animating) setSelectedIndex(i)
            }}
            selected={i === selectedIndex}
          />
        ))}
      </Slides>
    </Container>
  )
}

export default ImageAnimation
