const moveDraggable = (draggable) => {
  const correctPosition = document
    .getElementById(draggable.target || draggable.source)
    .getBoundingClientRect()
  draggable.left = correctPosition.x
  draggable.top = correctPosition.y
}

const DraggableImage = ({
  index,
  image,
  targetIds,
  correctTarget,
  onDrag = () => null,
}) => {
  const windowSize = useWindowSize()
  const imagePlaceholderId = `image${index}`
  const draggableId = `draggable${index}`
  const draggableDomRef = useRef()
  const draggableRef = useRef()
  useEffect(() => {
    if (!draggableDomRef || !draggableDomRef.current) return
    const d = new PlainDraggable(draggableDomRef.current)
    d.containment = document.getElementById("root")
    d.source = imagePlaceholderId
    draggableRef.current = d
    let snapped = false
    const targets = targetIds.map((x) => document.getElementById(x))
    d.snap = {
      targets,
      gravity: 50,
      edge: "inside",
    }
    d.onDrag = (moveTo) => {
      snapped = moveTo.snapped
      onDrag(true)
    }
    d.onDragEnd = (moveTo) => {
      if (snapped) {
        if (d.line) d.line.remove()
        d.target = findTarget(targets, moveTo)
        if (imagePlaceholderId && d.target) {
          d.line = Connection(imagePlaceholderId, draggableId, {
            options: {
              color: correctTarget === d.target ? "green" : "red",
            },
          })
        } else {
          console.log("no valid target")
        }
      } else {
        reset()
      }
      onDrag(false)
      moveDraggable(d)
    }

    const reset = () => {
      if (d) {
        if (d.line) {
          d.line.remove()
          d.line = null
        }
        d.target = null
      }
    }
    const cleanup = () => {
      if (d) {
        reset()
        d.remove()
      }
    }

    moveDraggable(d)
    return cleanup // Remove line on unmount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  useEffect(() => moveDraggable(draggableRef.current), [windowSize])
  return (
    <>
      <DraggableImagePlaceholder
        id={imagePlaceholderId}
        src={`match-img/${image}`}
        draggable={false}
        alt={image}
      />
      <DraggableImageElement
        src={`match-img/${image}`}
        id={draggableId}
        alt={image}
        ref={draggableDomRef}
      />
    </>
  )
}

const DraggableContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
`

const DraggableTargetDrop = styled(DraggableImageBase)`
  background-color: #ddd;
  border: 1px dashed black;
`

const DraggableTarget = ({ id, image, dragging }) => {
  return (
    <div style={{ display: "flex", flexDirection: "row" }}>
      <DraggableTargetDrop id={id} dragging={dragging} />
      <DraggableImageBase
        src={`match-img/${image}`}
        alt={image}
        style={{ marginLeft: 10 }}
      />
    </div>
  )
}

const SideContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: slategray;
  padding: 25px;
  border-radius: 10px;
  margin: 20px 0;
`

const toKey = (x, index) => `${x}_${index}`

const Title = styled.div`
  margin-top: 50px;
  font-size: 3rem;
  text-shadow: 2px 2px 2px slategray;
`

const titles = {
  learn: "Learn to Match",
  easy: "Easy Match Game",
  hard: "Hard Match Game",
}

const MatchLayout = ({ matches, gameMode }) => {
  const findTargetId = (image) =>
    Object.keys(matches).find((key) => matches[key] === image)

  const sources = Object.values(matches)
  const targetIds = shuffle(Object.keys(matches))
  return (
    <DraggableContainer>
      <SideContainer>
        {sources.map((x, i) => (
          <DraggableImage
            key={toKey(x, i)}
            index={i}
            image={x}
            targetIds={targetIds}
            correctTarget={findTargetId(x)}
          />
        ))}
      </SideContainer>
      <Title>{titles[gameMode] || "Match Game"}</Title>
      <SideContainer key={new Date().getTime()}>
        {targetIds.map((x, i) => (
          <DraggableTarget key={toKey(x, i)} id={x} image={x} />
        ))}
      </SideContainer>
    </DraggableContainer>
  )
}

const Match = ({ gameMode = "easy", matchCount = 5 }) => {
  const allImages = useMemo(
    () => ({
      "bird_image_1.png": {
        match: {
          learn: "bird_image_1.png",
          easy: "match_game_cifar_robust_images_bird_threshold_512.png",
          hard: "match_game_cifar_diluted_robust_images_bird_threshold_512.png",
        },
      },
      "cat_image_18.png": {
        match: {
          learn: "cat_image_18.png",
          easy: "match_game_cifar_robust_images_cat_threshold_512.png",
          hard: "match_game_cifar_diluted_robust_images_cat_threshold_512.png",
        },
      },
      "deer_image_13.png": {
        match: {
          learn: "deer_image_13.png",
          easy: "match_game_cifar_robust_images_deer_threshold_512.png",
          hard: "match_game_cifar_diluted_robust_images_deer_threshold_512.png",
        },
      },
      "dog_image_16.png": {
        match: {
          learn: "dog_image_16.png",
          easy: "match_game_cifar_robust_images_dog_threshold_512.png",
          hard: "match_game_cifar_diluted_robust_images_dog_threshold_512.png",
        },
      },
      "horse_image_16.png": {
        match: {
          learn: "horse_image_16.png",
          easy: "match_game_cifar_robust_images_horse_threshold_512.png",
          hard:
            "match_game_cifar_diluted_robust_images_horse_threshold_512.png",
        },
      },
      "boat_image_10.png": {
        match: {
          learn: "boat_image_10.png",
          easy: "match_game_cifar_robust_images_boat_threshold_512.png",
          hard: "match_game_cifar_diluted_robust_images_boat_threshold_512.png",
        },
      },
    }),
    []
  )
  const gameImageKeys = shuffle(Object.keys(allImages)).slice(0, matchCount)
  const matches = gameImageKeys.reduce(
    (all, x) => ({ ...all, [x]: allImages[x].match[gameMode] }),
    {}
  )
  return <MatchLayout matches={matches} gameMode={gameMode} />
}

export default Match
