const MembershipInferenceAttack = (props) => {
  const { status } = props
  return (
    <div>
      <ScrollyHeaderSection>
        Membership Inference Attack (MIA)
      </ScrollyHeaderSection>
      <ScrollContainer2>
        <ScrollyContentSection style={{ flexBasis: "70%" }}>
          <MiaFlowChart
            status={status}
            style={{ position: "sticky", top: 10 }}
          />
        </ScrollyContentSection>
        <ScrollyContentSection style={{ flexBasis: "30%" }}>
          <p>
            In the membership inference attack, we want to answer the question
            of whether this specific piece of data was in the training data set
            for the machine learning model. Here, one does not necessarily need
            to not have access to the original data set, though we have the
            ability to query the model with input data and obtain the outputs.
          </p>
          <p>
            There are several operational scenarios in which we need to answer
            this question. First, we may have a deployed model that has been
            supporting an application for some time. If the data point was used
            to train that model, it could make the model noncompliant and we
            would have to terminate its use. Furthermore, any actions previously
            taken or data previously collected based upon the model could also
            be noncompliant. Right now, legal precedence says to throw out the
            model and any evidence that may have been collected with it.
          </p>
          <p>
            Second, you may have a trained a model using a small amount of data
            that was collected with NTM and as such, it has a higher
            classification given its relationship to intent, sources, methods
            and TTPs. You do not own the data and it others naturally have valid
            access to it. Also, this model will be deployed into 3rd party
            spaces that you do not control. If that sensitive data can be
            readily identified from the model output, it puts NTM at risk.
          </p>
        </ScrollyContentSection>
      </ScrollContainer2>
    </div>
  )
}

