const cleanupLeaderLines = () =>
  // Due to animation, some leader lines are left behind
  Array.from(document.getElementsByClassName("leader-line")).forEach((x) =>
    x.remove()
  )

const Story = () => {
  const [viewState, setViewState] = useState({})

  useEffect(() => {
    return () => cleanupLeaderLines()
  }, [])

  const onStepEnter = ({ data }) => {
    const updated = { ...viewState, currentView: data }
    if (!(data in updated)) updated[data] = {}
    updated[data].visible = true
    setViewState(updated)
  }

  const onStepExit = ({ data }) => {
    const updated = { ...viewState }
    if (!(data in updated)) updated[data] = {}
    updated[data].visible = false
    setViewState(updated)
  }

  const onStepProgress = ({ data, progress }) => {
    const updated = { ...viewState }
    if (!(data in updated)) updated[data] = {}
    updated[data].visible = true
    updated[data].progress = progress
    setViewState(updated)
  }

  return (
    <>
      <ScrollContainer>
        <Scroller>
          <Scrollama
            onStepEnter={onStepEnter}
            onStepExit={onStepExit}
            progress
            onStepProgress={onStepProgress}
          >
            <Step data="weakness">
              <div>
                <WeaknessWithAI status={viewState["weakness"]} />
              </div>
            </Step>
            <Step data="puppies-or-food">
              <div>
                <PuppiesOrFood status={viewState["puppies-or-food"]} />
              </div>
            </Step>
            <Step data="mia">
              <div>
                <MembershipInferenceAttack status={viewState["mia"]} />
              </div>
            </Step>
            <Step data="20q">
              <div
                style={{
                  marginBottom: "10vh",
                }}
              >
                <TwentyQuestions status={viewState["20q"]} />
                <Practice demo />
              </div>
            </Step>
            <Step data="model-inversion-attack">
              <div>
                <ModelInversionAttack
                  status={viewState["model-inversion-attack"]}
                />
              </div>
            </Step>
          </Scrollama>
        </Scroller>
      </ScrollContainer>
    </>
  )
}

export default Story
