import LeaderLine from "leader-line-new"

export const clean = (obj) =>
  Object.keys(obj)
    .filter((k) => obj[k] != null)
    .reduce((a, k) => ({ ...a, [k]: obj[k] }), {})

export const styleDiv = (getDiv, style) => {
  const div = getDiv()
  Object.keys(style).forEach((k) => (div.style[k] = style[k]))
}

export const isDef = (x) => x !== null && x !== undefined

// https://stackoverflow.com/a/56749849/1072068
export const shuffle = (arr) =>
  [...arr].reduceRight(
    (
      res,
      _,
      __,
      s // eslint-disable-next-line no-sequences
    ) => (res.push(s.splice(0 | (Math.random() * s.length), 1)[0]), res),
    []
  )

export const Connection = (fromId, toId, config) => {
  const configOverrides = {
    ...{ options: {}, drawMode: "draw" },
    ...(config || {}),
  }
  const lineOptions = {
    start: document.getElementById(fromId),
    end: document.getElementById(toId),
    hide: true,
    path: "straight",
    startSocket: "right",
    endSocket: "left",
    ...configOverrides.options,
  }
  const l = new LeaderLine(lineOptions)
  l.show(configOverrides.drawMode)
  return l
}

export const HoverConnection = (fromId, toId, config) => {
  const configOverrides = {
    ...{ options: {}, drawMode: "draw" },
    ...(config || {}),
  }

  const lineOptions = {
    // start: document.getElementById(fromId),
    start: LeaderLine.mouseHoverAnchor(document.getElementById(fromId)),
    end: LeaderLine.pointAnchor(document.getElementById(toId), {
      x: 60,
      y: 20,
    }),
    hide: true,
    path: "straight",
    startPlug: "square",
    startSocket: "right",
    endSocket: "left",
    ...configOverrides.options,
  }
  const l = new LeaderLine(lineOptions)
  l.show(configOverrides.drawMode)
  return l
}

export const clearLines = (linesRef, noAnimate) => {
  if (!linesRef.current) return
  const lineKeys = Object.keys(linesRef.current.lines || {})
  lineKeys.forEach((x) => {
    linesRef.current.lines[x].remove()
    // if (noAnimate) linesRef.current.lines[x].remove()
    // else linesRef.current.lines[x].hide("draw")
    delete linesRef.current.lines[x]
  })
}

export const drawLine = (fromId, toId, linesRef, config) => {
  if (!linesRef.current) return
  const key = `${fromId}-${toId}`
  if (!linesRef.current.lines) linesRef.current.lines = {}
  if (linesRef.current.lines && linesRef.current.lines[key]) {
    linesRef.current.lines[key].remove()
  }
  linesRef.current.lines[key] = Connection(fromId, toId, config)
}

export const chain = async (fns, duration) => {
  const delay = (t) =>
    new Promise((resolve) => {
      setTimeout(() => {
        resolve()
      }, t)
    })

  for (let x of fns) {
    x()
    await delay(duration || 0)
  }
}
